body {
  margin:0;
  padding:0;
  background-color:white;    
  background-repeat:no-repeat;
  background-size:cover;
  background-attachment: fixed;  
}
body {
  font-family: 'Open Sans', sans-serif;
  color:#333333;
}
button {
	font-family: 'Open Sans', sans-serif;
	font-size:15px;
	cursor:pointer;
	outline:none;
}

blockquote {
  font-family: 'Source Sans Pro', sans-serif;  
}
@keyframes pulse {  
  from {  transform: rotate(0deg) translateX(1px) rotate(0deg);  }
  to   {  transform: rotate(360deg) translateX(1px) rotate(-360deg);  }  
}
.nav-component-heading {  
	text-align:center;
  font-size:3.6em; 
  padding-top:0.5em;
	margin:0 auto;  
	position:relative;	
	font-weight:normal;
}
.nav-component-hr {
  margin:0 auto;
  border:none;
  height:2px;
  width:50%;
  background: linear-gradient(to left, transparent, #101010, transparent);
}

@media (max-width:600px) {
  .nav-component-heading {  
    font-size:2.5em;
  }
}